import Abstract from './abstract';

class CredentialsBase extends Abstract {
  Create(data) {
    return this.request.send(`${this.endpoint}`, 'POST', data);
  }

  Delete(id) {
    return this.request.send(`${this.endpoint}/${id}`, 'DELETE');
  }

  Update(id, body) {
    let parsedBody = body;

    // Add `id` key to `body` object if not included
    if (!('id' in body)) {
      parsedBody = Object.assign(body, {
        id: id
      });
    }

    return this.request.send(`${this.endpoint}/${id}`, 'PUT', parsedBody);
  }
}

export default CredentialsBase;
